import { useEffect } from "react";


function Mypage() {

    useEffect(()=>{
        window.scrollTo(0,0)  // 최상단 이동
    },[])

    return (
        <div style={{width:'360px', height: '750px', margin:'0 auto'}} className="mb-5">
            <h4 className="mt-5 mb-5">MyPage</h4>            
            <p><b style={{color:'palevioletred'}}>{localStorage.getItem('springStar_nick')}</b> 님이 만족하실 때까지...</p>
            <img className="mypage"></img>
            <p>불편사항은 고객 게시판에 남겨 주시면...</p>
        </div>
    )
}


export default Mypage;