import { createSlice } from '@reduxjs/toolkit'

let user = createSlice({       // useState() 역할 임 (state 하나를 slice라고 부름)
    name: 'user',
    initialState: { name: '홍길동', age: 20 },
    reducers: {
        changeName(state){
            return { name: '임꺽정', age: 22 }
        },
        increase(state, a){
            state.age += a.payload
        }
    }
})
export let { changeName, increase } = user.actions;
export default user;